.category {
  display: flex;
  flex-direction: column;
  min-width: 280px;
  max-width: 1200px;
  margin: 0 auto;
}

.category div {
  display: flex;
  justify-content: center;
  gap: 5px;
  width: 100%;
  height: 150px;
  align-items: center;
}

.category h2 {
  padding-block: 1rem;
}
